select {
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none'%3E%3Cpath d='M8 9l4-4 4 4m0 6l-4 4-4-4' stroke='%239fa6b2' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") !important;
    line-height: 1.5rem;
    background-position-x: 99%;
    background-repeat: no-repeat;
    background-position-y: 5px;
    background-size: 24px;
    min-height: 38px;
}

select::-ms-value {
    background-image: unset;
}

.select__menu-list::-webkit-scrollbar {
    width: 4px;
    height: 0;
}

.select__menu-list::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.select__menu-list::-webkit-scrollbar-thumb {
    background: #888;
}

.select__menu-list::-webkit-scrollbar-thumb:hover {
    background: #555;
}
